import { AfterViewInit, Component, Input, NgZone, OnInit, Output, EventEmitter } from '@angular/core';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks';
import { VhType } from 'src/app/cafe/interface/vh-type';

@Component({
  selector: 'app-product-wishlist1',
  templateUrl: './product-wishlist1.component.html',
  styleUrls: ['./product-wishlist1.component.scss']
})
export class ProductWishlist1Component implements OnInit {
  @Output('productChoice') productChoice: EventEmitter<any> = new EventEmitter()
  indexPage: number = 1;
  wishList: any[] = []
  choiceFood: any = {};
  showOptions: boolean = false
  constructor(
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm
  ) { }

  ngOnInit(): void {
    let dataProd: any[] = this.vhQueryCafe.getlocalProducts().filter(product => product.selling && product.type == VhType.DISHES && !product.delete_hidden)
    this.wishList = dataProd
  }

  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index
  }

  chooseProduct(item) {
    this.productChoice.emit(item)
  }

  changeSub(value) {
    let subChoice: any = this.choiceFood.subs.find(item => item._id == value)
    if (subChoice) {
      this.choiceFood['id_subproduct'] = value
      this.choiceFood['price'] = subChoice.price
      this.choiceFood['price_origin'] = subChoice.price_origin
    }
  }

  selectFoods(value) {
    if (value.manysize) {
      this.choiceFood = {
        ...value, quantity: 1, note: '', price: 0, price_origin: 0, subs: value.subs.map(item => {
          return {
            ...item, price_origin: item.price,
            price: item.price
          }
        })
      }
      if (this.choiceFood.subs && this.choiceFood.subs.length) {
        this.choiceFood['id_subproduct'] = this.choiceFood.subs[0]._id
        this.choiceFood['price'] = this.choiceFood.subs[0].price
        this.choiceFood['price_origin'] = this.choiceFood.subs[0].price_origin
      }
    } else this.choiceFood = {
      ...value, quantity: 1, note: "", price_origin: value.price,
      price: value.price
    }
    // Lu tr cc properties  chon
    if (this.choiceFood.properties) {
      // sao chp mng gi tr chn
      this.choiceFood.properties.forEach(element => {
        element.active = true;
        element.selectedValue = element.value[0]
      });
    }
    // Kim tra topping
    if (this.choiceFood.toppings) {
      for (let topping of this.choiceFood.toppings) {
        topping.quantity = 0
        topping.price_origin = topping.price
        topping.price = topping.price_origin
      }
    }

    if (this.choiceFood) {
      this.showOptions = true;
    }
  }

  selectFoodsCancel() {
    this.showOptions = false;
  }

  chooseFood() {
    this.showOptions = false;
    if (this.choiceFood.quantity) {
      if (this.choiceFood.id_subproduct) {
        let sub: any = this.choiceFood.subs.find(item => item._id == this.choiceFood.id_subproduct)
        this.choiceFood['name'] += ` (${sub.name})`
      }
      this.productChoice.emit(this.choiceFood)
    }
  }

}
