import { Component, EventEmitter, HostListener, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm, VhQueryCafe, VhAuth } from 'ionic-vhframeworks';
import { VhOrderInvoices } from 'src/app/cafe/interface/vh-order-invoice';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { NzModalService } from 'ng-zorro-antd/modal';
import { ProductService } from 'src/app/cafe/services/product.service';
import { VhType } from 'src/app/cafe/interface/vh-type';

@Component({
  selector: 'app-bill-detail1',
  templateUrl: './bill-detail1.component.html',
  styleUrls: ['./bill-detail1.component.scss']
})
export class BillDetail1Component implements OnInit {
  @HostListener('window:keydown', ['$event'])
  keyEvent(event: KeyboardEvent) {
    if (["F3", "F4", "F5", "F6"].includes(event.key)) {
      this.closeSearchProduct(false)
      this.closeSearchService(false)
      this.closeSearchSideDishes(false)
      this.closeSearchCombos(false)

      switch (event.key) {
        case "F3": this.openSearchService(); break;
        case "F4": this.openSearchProduct(); break;
        case "F5": this.openSearchSideDishes(); break;
        case "F6": this.openSearchCombos(); break;
      }
      event.preventDefault(); event.stopPropagation()
    }
  }
  @Input() invoices: VhOrderInvoices
  @Input() tableInfo: any;
  @Output() deletePromotion: EventEmitter<boolean> = new EventEmitter()
  @Output() showProductGift: EventEmitter<boolean> = new EventEmitter()
  @Output() showProductGiftType7: EventEmitter<boolean> = new EventEmitter()
  @Output() showModalChangePriceType: EventEmitter<boolean> = new EventEmitter()
  @Output() checkPromotion: EventEmitter<boolean> = new EventEmitter()
  showDrawerProductList: boolean = false
  showDrawerServiceList: boolean = false
  showDrawerSideDishesList: boolean = false
  showDrawerCombosList: boolean = false
  showDrawerEdit: boolean = false
  display_promotion_selling_price: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').display_promotion_selling_price
  show_modal_select_lots = false;
  list_search_lot = [];
  warning_lot_expried: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').warning_lot_expried
  public printKitchen: any = {
    status: false,
    title: 'Add dishes',
    value: []
  };
  wishList: boolean = this.vhAuth.localStorageGET("show_best_selling");
  public VhType = VhType;



  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhAuth:VhAuth,
    private vhQueryCafe:VhQueryCafe,
    private vhComponent:VhComponent,
    private lang:LanguageService,
    private nzModalService:NzModalService,
    private productService: ProductService,
  ) { }

  ngOnInit(): void {
    this.wishList =this.vhAuth.localStorageGET("show_best_selling") ? this.vhAuth.localStorageGET("show_best_selling"): false;
    if(this.invoices.getInvoiceDetail().length && this.productService.isContainServiceTime(this.invoices)) {
      this.endTime = new Date();
      // set li price
      this.invoices.getInvoiceDetail()?.forEach((item, index) => {
        if (item.ptype === VhType.SERVICETIME && !item.is_paused) {
          const newItem = { ...item, time_end_order: this.endTime };
          const serviceTimePrice: any = this.productService.calculateServiceTimePrice(newItem, this.endTime, this.invoices.getPriceType());
          newItem.price = serviceTimePrice.total_price;
          newItem.price_origin = serviceTimePrice.total_price;
          this.invoices.getInvoiceDetail()[index] = {...newItem};
        }
      });
      // Tnh li total cho bill
      this.invoices.setSubTotal();
    }
  }

  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index
  }
  myTrackByNameFunction(element: any, index: any) {
    return element.name ? element.name : index
  }

  openSearchProduct() {
    this.showDrawerProductList = true
  }
  closeSearchProduct(ev) {
    if (ev) this.createBillDetail(ev)
    else this.showDrawerProductList = false
  }
  openSearchService() {
    this.showDrawerServiceList = true
  }
  closeSearchService(ev) {
    if (ev) this.createBillDetail(ev)
    else this.showDrawerServiceList = false
  }
  openSearchSideDishes() {
    this.showDrawerSideDishesList = true
  }
  closeSearchSideDishes(ev) {
    if (ev) this.createBillDetail(ev)
    else this.showDrawerSideDishesList = false
  }
  openSearchCombos() {
    this.showDrawerCombosList = true
  }
  closeSearchCombos(ev) {
    if (ev) this.createBillDetail(ev)
    else this.showDrawerCombosList = false
  }

  createBillDetail(productValue: any) {
    console.log(productValue);
    
    // mn khng c sub
    if (productValue.quantity) {
      let name = `${productValue.name}`
      let detail: any = this.invoices.getCreateItemDetail(productValue,
        productValue.id_subproduct ?
          {
            _id: productValue.id_subproduct,
            quantity: productValue.quantity,
            price: productValue.price, price_origin: productValue.price_origin,
            note: productValue.note
          } : null
        , true)
      this.invoices.addInvoiceDetail({ ...productValue,id_product: productValue._id, name })
      
    }
  }
  /**Cp nht thng tin mn n */
  saveEdit() {
    if(this.choiceFood.ptype === VhType.SERVICETIME) {
      if (!this.choiceFood.is_paused) {
        this.choiceFood.time_end_order = new Date();
      }
      const serviceTimePrice: any = this.productService.calculateServiceTimePrice(this.choiceFood, this.endTime, this.invoices.getPriceType());
      this.choiceFood.price = serviceTimePrice.total_price;
      this.choiceFood.price_origin = serviceTimePrice.total_price;
      delete this.choiceFood.serviceTimePrice;
    }
    this.showDrawerEdit = false;

    let index: number = this.invoices.getInvoiceDetail().findIndex(item => item._id == this.choiceFood._id);

    this.invoices.getInvoiceDetail()[index] = { ...this.choiceFood }
    this.invoices.setSubTotal()
    
    delete this.choiceFood
  }

  editPrice: any = {}
  choiceFood: any;
  editFood(item: any) {
    this.choiceFood = item
    this.showDrawerEdit = true;

    // Nu khng phi Dch v thi gian
    if(this.choiceFood.ptype !== VhType.SERVICETIME) {
      // backup
      // cleavejs cho cc input
      setTimeout(() => {
        this.editPrice['price-' + item.id_product] = this.vhAlgorithm.vhnumeral(".price-" + item.id_product);
        if (this.choiceFood.toppings) {
          this.choiceFood.toppings.forEach(topping => {
            this.editPrice['topping-' + topping.id_product] = this.vhAlgorithm.vhnumeral(".topping-" + topping.id_product);
          });
        }
      }, 200);
      return;
    }

    // Nu l Dch v thi gian
    this.productService.updateServiceTimePrice(this.choiceFood, this.invoices.getPriceType());
    if(!this.choiceFood.is_paused) {
      this.invoices.setSubTotal();
    }
    console.log(this.choiceFood);
  }



  /**Thm cleavejs chnh sa gi mn n */
  updatePrice(item) {
    if (item.ptype) {
      let price = this.editPrice['price-' + item.id_product].getRawValue()
      this.choiceFood.price = price ? parseFloat(price) : 0
    } else {
      let price = this.editPrice['topping-' + item.id_product].getRawValue()
      let index = this.choiceFood.toppings.findIndex(element => element.id_product == item.id_product)
      if (index != -1) this.choiceFood.toppings[index].price = price ? parseFloat(price) : 0
    }
  }
  /**Xa mn n */
  removeFood(food: any) {
    this.invoices.removeInvoiceDetail(food._id)
    
  }


  getUnit(units: any[], ratio) {
    return this.vhQueryCafe.getUnit_byRatio(units, ratio).unit
  }
  searchLot(value) {
    this.choiceFood.lots = this.vhAlgorithm.searchList(this.vhAlgorithm.changeAlias(value), this.list_search_lot, ['lot_number'])
  }
  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id);
    return lot.lot_number
  }
  openModalChooseLot(data) {
    this.choiceFood = data
    this.list_search_lot = this.choiceFood.lots
    if (!this.list_search_lot.length) this.vhComponent.alertMessageDesktop("error", this.lang.translate("Sn phm ny khng c l kh dng  bn!"))
    else this.show_modal_select_lots = true;
  }
  handleChooseLot() {
    if (this.warning_lot_expried) {
      let lot_select = this.list_search_lot.find(e => e._id == this.choiceFood.id_lotproduct)
      if (new Date().getTime() >= new Date(lot_select.date_exp).getTime()) {
        this.nzModalService.confirm({
          nzTitle: this.lang.translate("L ny  ht hn! Tip tc bn sn phm ny?"),
          nzOnOk: () => {
            this.show_modal_select_lots = false;
            this.choiceFood.lots = this.list_search_lot;
            this.vhQueryCafe.updateBill_Detail(this.choiceFood._id, { id_lotproduct: this.choiceFood.id_lotproduct })
          },
          nzOnCancel: () => {
            // this.show_modal_select_lots = false;
            // this.choiceFood.lots = this.list_search_lot;
          }

        })
      }
      else {
        this.show_modal_select_lots = false;
        this.choiceFood.lots = this.list_search_lot;
        this.vhQueryCafe.updateBill_Detail(this.choiceFood._id, { id_lotproduct: this.choiceFood.id_lotproduct })
      }
    }
    else {
      this.show_modal_select_lots = false;
      this.choiceFood.lots = this.list_search_lot;
      this.vhQueryCafe.updateBill_Detail(this.choiceFood._id, { id_lotproduct: this.choiceFood.id_lotproduct })
    }
  }

  handleCancelChooseLot() {
    this.show_modal_select_lots = false;
    this.choiceFood.lots = this.list_search_lot
  }



  /* -------------------------------------------------- MODAL CHNH SA THI GIAN S DNG ------------------------------------------------- */
  /** C hin modal Chnh sa thi gian s dng hay khng */
  isShowModalEditUsedTime: boolean = false;
  /** Bin tm lu thi gian bt u (time_start_order) */
  tempStartTime: any;
  /** Bin tm lu thi gian kt thc (time_end_order) */
  tempEndTime: any;
  /** Thi gian m bill, dng  tnh thi gian cho Dch v thi gian */
  endTime: Date = new Date();



  /**
   * M modal Chnh sa thi gian s dng
   */
  handleOpenEditUsedTime() {
    this.tempStartTime = this.choiceFood.time_start_order;
    if (!(this.tempStartTime instanceof Date)) {
      this.tempStartTime = new Date(this.tempStartTime);
    }
    if(this.choiceFood.is_paused) {
      this.tempEndTime = this.choiceFood.time_end_order;
      if (!(this.tempEndTime instanceof Date)) {
        this.tempEndTime = new Date(this.tempEndTime);
      }
    }
    else {
      this.tempEndTime = null;
    }
    this.isShowModalEditUsedTime = true;
  }



  /**
   * Khi nhn ok trong modal Chnh sa thi gian s dng
   */
  handleUpdateUsedTime() {
    // validate start time<= now; end time > start time
    if (this.tempStartTime > new Date()) {
      this.vhComponent.alertMessageDesktop('error', this.lang.translate('The start time must be less than or equal to the current time.'));
      return;
    }
    if (this.tempEndTime && this.tempEndTime <= this.tempStartTime) {
      this.vhComponent.alertMessageDesktop('error', this.lang.translate('The end time must be greater than the start time.'));
      return;
    }

    this.isShowModalEditUsedTime = false;

    this.choiceFood.time_start_order = this.tempStartTime;

    const currentTime = new Date();
    if(!this.tempEndTime) {
      this.choiceFood.is_paused = false;
    }
    else {
      // mt hng tnh gi s dng tnh gi ngay ti thi im 
      this.choiceFood.is_paused = true;
    }

    this.choiceFood.time_end_order = this.tempEndTime ?? currentTime;

    this.vhQueryCafe.updateBill_Detail(this.choiceFood._id, { 
      time_start_order: this.tempStartTime,
      time_end_order: this.tempEndTime ? this.choiceFood.time_end_order : null,
      is_paused: this.choiceFood.is_paused,
    });

    // Tnh li gi cho dch v thi gian
    const serviceTimePrice: any = this.productService.calculateServiceTimePrice(this.choiceFood, this.endTime, this.invoices.getPriceType());
    this.choiceFood.serviceTimePrice = JSON.parse(JSON.stringify(serviceTimePrice));
    this.choiceFood.price = serviceTimePrice.total_price;
    this.choiceFood.price_origin = serviceTimePrice.total_price;
    this.invoices.setSubTotal();
  }



  /**
   * Tm ngng / Tip tc tnh gi
   * 
   * @param isPause true: tm ngng; false: tip tc tnh gi
   */
  pauseTime(isPause: boolean) {
    this.choiceFood.time_end_order = new Date();
    this.choiceFood.is_paused = isPause;

    const serviceTimePrice: any = this.productService.calculateServiceTimePrice(this.choiceFood, this.endTime, this.invoices.getPriceType());
    this.choiceFood.serviceTimePrice = JSON.parse(JSON.stringify(serviceTimePrice));
    this.choiceFood.price = serviceTimePrice.total_price;
    this.choiceFood.price_origin = serviceTimePrice.total_price;
    this.invoices.setSubTotal();

    this.vhQueryCafe.updateBill_Detail(this.choiceFood._id, { 
      time_end_order: isPause ? this.choiceFood.time_end_order : undefined, 
      is_paused: isPause ,
      price: this.choiceFood.price,
    });
  }

}
