import { AfterViewInit, Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks';
import { NzCascaderOption } from 'ng-zorro-antd/cascader';
import { VhOrderInvoices } from 'src/app/cafe/interface/vh-order-invoice';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { ProductService } from 'src/app/cafe/services/product.service';

@Component({
  selector: 'app-bill1',
  templateUrl: './bill1.component.html',
  styleUrls: ['./bill1.component.scss']
})
export class Bill1Component implements OnInit, AfterViewInit, OnChanges {
  @Input() invoices: VhOrderInvoices
  @Input() debt: boolean = false;
  @Input() tax: number = 0
  @Input() paymentType: Array<any> = [1]
  @Input() id_partner :any = ''
  /** C ngn edit hay khng, c th s hin th cc input v textarea di dng span */
  @Input() isPreventEdit: boolean = false;
  nzOptions: NzCascaderOption[] = [
    { value: 1, label: this.languageService.translate("Cash"), isLeaf: true },
    { value: 2, label: this.languageService.translate("Debit"), disabled: true, isLeaf: true },
    { value: 3, label: this.languageService.translate("Account"), children: [] }
  ]
  listTax: any[] = this.vhQueryCafe.getlocalTaxs().filter(tax => tax.status);
  printer:any;
  moneyEdit: any = {}
  percent_discount : number = 0; 
  constructor(
    private languageService: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    public vhAlgorithm: VhAlgorithm,
    private productService: ProductService,
  ) { }

  ngOnInit(): void {
    // ly thng tin ti khon
    this.getWallets()
    
    this.printer = this.vhQueryCafe.getlocalPrinters().find(item => item.name == "DESKTOP") ? this.vhQueryCafe.getlocalPrinters().find(item => item.name == "DESKTOP").sales :  {show_discount : true, show_fee: true, show_tax: true,show_note : true, show_rest : true}
  }

  ngOnChanges({ debt ,id_partner}: SimpleChanges) {
    if (debt) {
      if (!debt.currentValue) {
        if (this.paymentType[0] == 2) this.changePaymentType([1])
      }
      this.nzOptions[1].disabled = !debt.currentValue
    }
    this.percent_discount = this.invoices.getDiscount()/this.invoices.getSubTotal()*100
    
  }

  ngAfterViewInit() {
    // Cleave js
    if(document.querySelector(".discount-price"))  this.moneyEdit['discount'] = this.vhAlgorithm.vhnumeral(".discount-price")
    if(document.querySelector(".fee-price")) this.moneyEdit['fee'] = this.vhAlgorithm.vhnumeral(".fee-price")
    if(document.querySelector(".payment-price")) this.moneyEdit['payment'] = this.vhAlgorithm.vhnumeral(".payment-price")
  }

  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index
  }

  getWallets() {
    this.nzOptions[2].children = this.vhQueryCafe.getlocalWallets()
      .filter(wallet => wallet._id != 'id_cash' &&
        wallet.id_branch == this.vhQueryCafe.getDefaultBranch()._id
      ).map(wallet => {
        return { label: wallet.name, value: wallet._id, isLeaf: true }
      })
  }


  changePercentDiscount(value){
    this.percent_discount = value
    this.invoices.setDiscount(this.invoices.getSubTotal()*value/100) 
  }
  /**Cp nht gim gi */
  updateDiscount() {
    let discount: any = this.moneyEdit['discount'].getRawValue()
    this.invoices.setDiscount(discount ? parseFloat(discount) : 0)
    this.percent_discount = discount/this.invoices.getSubTotal()*100
  }
  editPercentDiscount(value) {
    if (parseFloat(value) > 100)
      document.getElementById("percent-discount-price")['value'] = 0
    else this.changePercentDiscount(value)
  }
  /** Cp nht ph */
  updateFee() {
    let fee: any = this.moneyEdit['fee'].getRawValue()
    this.invoices.setFee(fee ? parseFloat(fee) : 0)
  }
  /**Cp nht thu */
  updateTax(value) {
    this.invoices.setTax(parseFloat(value))
  }
  /**Cp nht tin khch tr */
  updatePayment() {
    if (this.debt) {
      let payment: any = this.moneyEdit['payment'].getRawValue()
      let value = payment ? parseFloat(payment) : 0

      this.invoices.setPayment(value ? (value > this.invoices.getTotalATax() || value < 0 ? this.invoices.getTotalATax() : value) : 0)

      document.getElementById("payment-price")['value'] = this.invoices.getPayment()
      this.moneyEdit['payment'] = this.vhAlgorithm.vhnumeral(".payment-price")
    }
  }
  /**Cp nht ghi ch */
  updateNote() {
    let note: any = document.getElementById("invoice-note")
    this.invoices.setNote(note.value)
  }

  /** Thay i phng thc thanh ton */
  changePaymentType(value: any) {
    this.invoices.setPaymentType(value[0])
    this.invoices.setPayment()
    switch (value[0]) {
      case 1:
        this.invoices.setIdWallet("id_cash")
        break
      case 2:
        this.invoices.setIdWallet("")
        break;

      case 3:
        this.invoices.setIdWallet(value[1])
        break;
    }
  }



  /**
   * Ly label ca phng thc thanh ton
   * @param value value ca phng thc thanh ton cn ly label
   * @returns label ca phng thc thanh ton 
   */
  getPaymentTypeLabel(value) {
    if(value[1]) {
      let wallet = this.vhQueryCafe.getlocalWallets().find(item => item._id == value[1]);
      if(wallet) return `${this.nzOptions.filter(item => item.value == value[0])?.[0]?.label} / ${wallet.name}`;
    }
    else {
      return this.nzOptions.filter(item => item.value == value[0])?.[0]?.label;
    }
  }



  /**
   * Tnh li bill detail cho Dch v thi gian v setSubTotal() cho invoices
   */
  updateServiceTime() {
    if(this.isPreventEdit || !this.productService.isContainServiceTime(this.invoices)) return;
    this.productService.updateServiceTimePriceInvoiceDetail(this.invoices.getInvoiceDetail(), this.invoices.getPriceType());
    this.invoices.setSubTotal();
  }

}
