import { AfterViewChecked, ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ActivatedRouteSnapshot, Router, RouterStateSnapshot, UrlTree } from '@angular/router';
import { VhAlgorithm, VhQueryCafe, VhAuth, VhEinvoice } from 'ionic-vhframeworks';
import { NzModalRef, NzModalService } from 'ng-zorro-antd/modal';
import { Observable } from 'rxjs';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { VhOrderInvoices } from 'src/app/cafe/interface/vh-order-invoice';
import { VhType } from 'src/app/cafe/interface/vh-type';
import { BillService } from 'src/app/cafe/services/bill.service';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { ProductService } from 'src/app/cafe/services/product.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit, AfterViewChecked {
  dataRestore: any;
  invoices: VhOrderInvoices = new VhOrderInvoices();
  // cho php n hay khng
  debt: boolean = false;
  // thng tin bn ang c t
  tableInfo: string = '';
  // trng thi
  statusInvoice: any = {
    updating: false,
    deleting: false
  }
  // thuc tnh my in
  printer: any;
  list_earning_point_bills: any = [];
  list_earning_points_products: any = [];
  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe,
    private router: Router,
    public vhAuth: VhAuth,
    private languageService: LanguageService,
    private vhComponent: VhComponent,
    private cdRef: ChangeDetectorRef,
    private modal: NzModalService,
    private productService: ProductService,
    private vhEinvoice: VhEinvoice,
    private billService: BillService,
  ) {
    let dataTrans: any = this.router.getCurrentNavigation().extras.state
    if (dataTrans.dataRestore) this.dataRestore = dataTrans.dataRestore
    this.initInvoice(dataTrans)
  }

  ngOnInit(): void {
    Promise.all([
      this.vhQueryCafe.getEarningPointsBills(),
      this.vhQueryCafe.getEarningPointsProducts(),

    ])
      .then(([earning_point_bills, earning_points_products]) => {
        this.list_earning_point_bills = earning_point_bills
        this.list_earning_points_products = earning_points_products;
        this.getClassAndProgram();
      })
  }

  ngAfterViewChecked() {
    this.cdRef.detectChanges()
  }
  /**Pop up bo khi pht sinh li */
  alertErr(err) {
    console.error(err)
    this.vhComponent.showToast(2000, "Li kt ni", "alert-toast")
  }

  /**Render food name */
  renderNameFood(id_prod: string, id_subProd?: string) {
    let food = this.vhQueryCafe.getlocalProduct(id_prod)
    let subFood: any;

    if (id_subProd)
      subFood = food.subs.find(item => item._id == id_subProd)
    return `${food ? food.name : ''} ${subFood ? '(' + subFood.name + ')' : ''}`
  }
  /**Render cc thnh phn combo */
  renderSubCombo(arr: any[]) {
    let subName: string = ""
    arr.forEach(item => {
      if (subName.length)
        subName += `, ${item.quantity} ${item.name}`
      else subName += `${item.quantity} ${item.name}`
    })
    return subName
  }
  /**Khi to ha n */
  initInvoice({ invoice, invoiceDetail }) {
    // ly thu 
    let tax: any = this.vhQueryCafe.getlocalTaxs().find(tax => tax.status && tax.default);
    if (tax) this.invoices.setTax(parseFloat(tax.value))
    if (invoice && invoiceDetail) {
      this.invoices = new VhOrderInvoices(invoice, invoiceDetail, this.vhQueryCafe)
      // kim tra cho php n
      this.debt = this.invoices.getIdPartner() == "id_retail" ? false :
        this.vhQueryCafe.getlocalCustomer(this.invoices.getIdPartner())
    }

    // ly thng tin bn
    if (this.invoices.getIdTable() == "go_home") {
      this.tableInfo = this.languageService.translate("Take away")
    } else {
      this.tableInfo = `${this.vhQueryCafe.getlocalArea(this.invoices.getIdArea()).name} - ${this.vhQueryCafe.getlocalTable(this.invoices.getIdTable()).name}`
    }

  }
  /**Save ha n */
  saveProcess() {
    this.productService.updateAllServiceTimeToPause(this.invoices.getInvoiceDetail());
    return this.vhQueryCafe.updateBill_Billdetail(this.invoices.getID(), this.invoices.getCreateUpdateInvoice(), this.invoices.getCreateUpdateInvoiceDetail())
  }
  saveInvoice() {
    this.statusInvoice['saving'] = true
    const bill: any = {...this.invoices};
    const invoiceTemp = {...bill?.invoice};
    this.saveProcess().then(async (res) => {
      if(this.cqt_submit_invoice_immediately) await this.handleMinvoice(invoiceTemp);
      this.vhComponent.showToast(2000,
        `${this.languageService.translate("Sales invoice")} ${this.invoices.getBillCode()} ${this.languageService.translate("has been updated successfully")}`, "success-toast"
      )
      this.exitPage = true;
      this.router.navigate(['/cafe/dashboard/management/invoice/order-invoice'], { state: { ...this.dataRestore, reload: true } })
    })

  }


  ///////////////////////////////////////////////GO BACK////////////////////////////////////////////////////
  exitPage: boolean = false;
  popUpExit() {
    return new Promise<{ save: boolean, exit: boolean }>(resolve => {
      const modal: NzModalRef = this.modal.create({
        nzTitle: this.languageService.translate("Confirm"),
        nzContent: this.languageService.translate("Are you sure to cancel the sales invoice") + "?",
        nzFooter: [
          {
            label: this.languageService.translate("Cancel"),
            onClick: () => {
              modal.destroy()
              resolve({ save: false, exit: false })
            }
          },
          {
            label: 'OK',
            type: 'primary',
            onClick: () => {
              modal.destroy()
              resolve({ save: false, exit: true })
            }
          }
        ]
      })
    })
  }
  goBack() {
    this.popUpExit().then(({ save, exit }) => {
      this.exitPage = exit;
      if (exit) {
        this.router.navigate(['/cafe/dashboard/management/invoice/order-invoice'], { state: this.dataRestore })
      }
    })
  }

  checkDeactivate(currentRoute: ActivatedRouteSnapshot, currentState: RouterStateSnapshot, nextState?: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
    if (!this.exitPage) {
      this.popUpExit().then(({ save, exit }) => {
        this.exitPage = exit;
        if (exit) {
          if (nextState.url != 'cafe/dashboard/management/invoice/order-invoice')
            this.router.navigate([nextState.url])
          else
            this.router.navigate(['/cafe/dashboard/management/invoice/order-invoice'], { state: this.dataRestore })
        }
      })
    } return this.exitPage;
  }


  /**
   * hm ny  xa tt c promotion trong bill v update
   */
  deletePromotions() {
    this.destroyBillDetails_withPromotions().then(() => {

    })
  }

  destroyBillDetails_withPromotions() {
    this.invoices.setBillHavePromotion(false)
    return new Promise((resolve) => {
      let bill_details = this.vhQueryCafe.destroyBillDetails_withPromotions(this.invoices.getInvoiceDetail()).map(item => { return { ...item, _id: item._id ? item._id : true } })
      console.log(bill_details);

      this.invoices = new VhOrderInvoices(this.invoices.getCreateUpdateInvoice(5), bill_details, this.vhQueryCafe)
      this.invoices.setSubTotal();
      this.product_gift_type7 = [];
      this.bill_details_pmtype_7 = [];
      resolve(true)
    })

  }
  isGotPromotion = false;
  showProductGift = false;
  product_gift_type7: any = [];
  showProductGiftType7 = false;
  gotten_gift_type7 = false;
  bill_details_pmtype_7: any = [];
  handleAddGiftType7() {
    this.showProductGiftType7 = false;
    if (!this.gotten_gift_type7)
      for (let i in this.product_gift_type7) {
        // this.product_gift_type7[i].products_gift = this.product_gift_type7[i].products_gift.filter(item => item.choose).map(e => { return { ...e } })
        if (this.product_gift_type7[i].products_gift.length) this.invoices.addBill_detail_promotion(this.product_gift_type7[i])
      }
    console.log(this.invoices.getInvoiceDetail());
    this.gotten_gift_type7 = true;
  }
  /**
   * hm ly qu
   */
  getPromotions() {
    this.gotten_gift_type7 = false;
    this.vhComponent.showLoading('')
      .then(() => {
        this.vhQueryCafe.changeBillDetails_withPromotions(this.invoices.getInvoiceDetail(), this.invoices.getDate()).then((bill_details_display: any) => {

          this.handleChooseLotForPromotion(bill_details_display.bill_details_pmtype_0to6)
          this.handleChooseLotForPromotion(bill_details_display.bill_details_pmtype_7)

          this.bill_details_pmtype_7 = this.vhAlgorithm.sortNumberbyASC([...bill_details_display.bill_details_pmtype_7], 'total_bill_value')
          this.isGotPromotion = true;
          this.vhComponent.hideLoading(0)
          this.invoices = new VhOrderInvoices(this.invoices.getCreateUpdateInvoice(5), [...bill_details_display.bill_details_pmtype_0to6,], this.vhQueryCafe,);
          // if (bill_details_display.filter(item => item.products_gift).length) this.showProductGift = true;
          this.getEarningpointPromotion();

          // this.product_gift_type7 = [...bill_details_display.bill_details_pmtype_7]
          this.handlePromotionType7(this.bill_details_pmtype_7)

        }, error => {
          this.vhComponent.hideLoading(0).then(() => {
            this.vhComponent.showToast(5000, error, "alert-toast")
          })
        })
      })
  }

  handlePromotionType7(bill_details_pmtype_7) {
    if (bill_details_pmtype_7.length) {
      if (bill_details_pmtype_7.findLastIndex(e => e.total_bill_value <= this.invoices.getTotalATax()) != -1)
        this.product_gift_type7 = [bill_details_pmtype_7[bill_details_pmtype_7.findLastIndex(e => e.total_bill_value <= this.invoices.getTotalATax())]]
      else this.product_gift_type7 = []
      this.showProductGiftType7 = true
    }
    else this.product_gift_type7 = []
    // console.log(this.product_gift_type7);
  }

  handleChooseLotForPromotion(promotion) {
    for (let i of promotion) {
      if (i.products_gift) {
        for (let product of i.products_gift) {
          if (product.lots && product.lots.length) {
            let min_date_exp = this.vhAlgorithm.sortDatebyASC(product.lots, 'date_exp')
            product['id_lotproduct'] = min_date_exp[0]._id;
            product['lot_number'] = min_date_exp[0].lot_number;
          }
          if (product.combos) {
            product.combos.forEach(element => {
              if (element.lots && element.lots.length) {
                let min_date_exp = this.vhAlgorithm.sortDatebyASC(element.lots, 'date_exp')
                element['id_lotproduct'] = min_date_exp[0]._id;
                element['lot_number'] = min_date_exp[0].lot_number;
              }
            });
          }
        }
      }
    }
  }

  /**
   * hm ny  get SubTotal mi ca mng  ly qu
   * ng thi gn im tch ly sn phm mi ca cc sn phm trong khuyn mi nu c
   */
  getEarningpointPromotion() {
    for (let i of this.invoices.getInvoiceDetail()) {
      if (i.pmtype) this.invoices.setBillHavePromotion(true);
      if (i.products_gift) {
        for (let product of i.products_gift) {
          product.earning_points_product = this.getTotalEarningPointProduct(i, true);
        }
      }
      if (i.products) {
        for (let product of i.products) {
          product.earning_points_product = this.getTotalEarningPointProduct(i, true)
        }
      }
      if (!i.id_promotion) i.earning_points_product = this.getTotalEarningPointProduct(i, null)
    }
    this.invoices.setSubTotal();
    this.vhQueryCafe.updateBill_Billdetail(this.invoices.getID(), this.invoices.getCreateUpdateInvoice(5), this.invoices.getCreateUpdateInvoiceDetail())
      .then(() => {
        console.log("success");
      })
  }

  /**
   * hm ny  kim tra disable sn phm tng hay ko
   * @param data phn t trong mng  ly qu
   * @returns
   */
  checkDisableChooseProductGift(data) {
    if (data.products_gift.filter(item => item.choose).length == data.promotions_maximum) return true;
    else return false
  }
  getUnit(units: any[], ratio) {
    return this.vhQueryCafe.getUnit_byRatio(units, ratio).unit
  }
  getLotNumber(lots, _id) {
    let lot = lots.find(item => item._id == _id);
    return lot.lot_number
  }



  /**
   * hm get im tch ly theo ha n
   * 1. get hng khch hng
   * 2. t hng khch hng get tch im theo bill
   * 3. check cc iu kin ca chng trnh
   * a check sn phm khuyn mi include_promotion_product
   * b  check gim gi/ chit khu include_discount
   * c check thanh ton = im thng include_paid_points
   * d check ph  include_fee
   * e check thu include_tax
   * @returns 0 | number
   */
  getEarningBill() {
    if (this.program_bill && this.invoices.getSubTotal() > this.program_bill.bill_total_minimum) {
      let subTotal = this.invoices.getSubTotal();
      if (!this.program_bill.include_promotion_product) { // ko tch im sp c km =>  tnh li subTotal
        subTotal = this.getSubTotalNotPromotion()
        if (this.program_bill.include_discount) subTotal = subTotal - this.invoices.getDiscount() - this.invoices.getDiscountBill()

        if (this.program_bill.include_paid_points && this.invoices.getDiscountPoint()) subTotal = subTotal - this.invoices.getDiscountPoint()
        if (this.program_bill.include_fee) subTotal += this.invoices.getFee();

        if (this.program_bill.include_tax) subTotal += subTotal * (this.invoices.getTax() / 100)
        return ((subTotal - subTotal % this.program_bill.exchange.money) / this.program_bill.exchange.money) * this.program_bill.exchange.points
      }
      else {
        subTotal = this.invoices.getSubTotal();
        if (this.program_bill.include_discount) subTotal = subTotal - this.invoices.getDiscount() - this.invoices.getDiscountBill()

        if (this.program_bill.include_paid_points && this.invoices.getDiscountPoint()) subTotal = subTotal - this.invoices.getDiscountPoint()
        if (this.program_bill.include_fee) subTotal += this.invoices.getFee();

        if (this.program_bill.include_tax) subTotal += subTotal * (this.invoices.getTax() / 100)
        return ((subTotal - subTotal % this.program_bill.exchange.money) / this.program_bill.exchange.money) * this.program_bill.exchange.points
      }
    }
    else return 0
  }


  /**
  * hm ny tr v tng tin ca sn phm ko c chng trnh khuyn mi
  * return : number
  */
  getSubTotalNotPromotion() {
    return this.invoices.getInvoiceDetail().filter(item => !item.id_promotion).reduce((prev: number, next) => prev + next.quantity * next.price, 0)
  }

  /**
   * hm ny tr v tng tin ca sn phm  ko c chng trnh khuyn mi thuc chng trnh tch im sn phm
   * return : number
   */
  getSubTotalNotPromotionOFProduct(products_of_program_earnig_product) {
    return this.invoices.getInvoiceDetail()
      .filter(item => item.price_origin == item.price && products_of_program_earnig_product.find(i => i == item.id_product || i == item.id_subproduct))
      .reduce((prev: number, next) => prev + next.quantity * next.price, 0)
  }


  /**
   * hm ny tr v tng im c tch ca tt c sn phm
   * @returns 0 | number
   */
  getEarningProduct(products) {
    if (this.program_product) {
      let subTotal = 0;
      if (!this.program_product.include_promotion_product) { // tch im sp ko c km 
        subTotal = this.getSubTotalNotPromotionOFProduct(this.program_product.products);
        return ((subTotal - subTotal % this.program_product.exchange.money) / this.program_product.exchange.money) * this.program_product.exchange.points
      }
      else {// tch im sp c km =>  tnh li subTotal
        subTotal = products.filter(item => item.price_origin == item.price && this.program_product.products.find(i => i == item.id_product)).reduce((prev: number, next) => prev + next.quantity * next.price, 0)
        return ((subTotal - subTotal % this.program_product.exchange.money) / this.program_product.exchange.money) * this.program_product.exchange.points
      }
    }
    else return 0
  }




  customer_class: any;
  program_bill: any;
  program_product: any;
  earning_point: number = 0;
  /**
  * hm ny  gn chng trnh tich diem ha n v sn phm ca khch hng
  */
  getClassAndProgram() {
    let customer = this.vhQueryCafe.getlocalCustomer(this.invoices.getIdPartner());
    this.customer_class = this.vhQueryCafe.getlocalCustomerClasss().find(item => item._id == customer.id_customer_class);
    if (this.customer_class && this.customer_class.id_earning_points_bill) {
      this.program_bill = this.list_earning_point_bills.find(item => item._id == this.customer_class.id_earning_points_bill);
    }
    if (this.customer_class && this.customer_class.id_earning_points_product) {
      this.program_product = this.list_earning_points_products.find(item => item._id == this.customer_class.id_earning_points_product);
    }
    if (!this.customer_class) {
      this.program_bill = null;
      this.program_product = null;
    }
  }
  /**
   * hm ny tr v tng im c tch ca n v sn phm
   * @returns 0 | number
   */
  getTotalEarningPoint() {
    this.earning_point = this.getEarningProduct(this.invoices.getInvoiceDetail()) + this.getEarningBill();
    return this.earning_point ? this.earning_point : 0
  }

  /**
   * hm ny tr v im c tch ca sn phm c thm
   * @param product sn phm c thm
   * @returns 0 | number
   */
  getTotalEarningPointProduct(product, promotion) {
    if (this.program_product) {
      let subTotal = 0;
      if (!this.program_product.include_promotion_product && promotion) { // tch im sp c km =>  tnh li subTotal
        subTotal = this.program_product.products.find(i => i == product._id) ? product.price : 0
        return ((subTotal - subTotal % this.program_product.exchange.money) / this.program_product.exchange.money) * this.program_product.exchange.points
      }
      else {
        subTotal = this.program_product.products.find(i => i == product._id) ? product.price : 0
        return ((subTotal - subTotal % this.program_product.exchange.money) / this.program_product.exchange.money) * this.program_product.exchange.points
      }
    }
    else return 0
  }



  /* ----------------------------------------------------------- HA N IN T ---------------------------------------------------------- */

  cqt_invoice_auto_sign: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').cqt_invoice_auto_sign
  cqt_submit_invoice_immediately: boolean = this.vhQueryCafe.getLocalAppSettingNameBranch('permission_branch').cqt_submit_invoice_immediately

  handleMinvoice(bill) {
    return new Promise((resolve, reject) => {
      // ly ra chi nhnh hin ti
      let currentBranch = this.vhQueryCafe.getlocalBranch(this.vhQueryCafe.getDefaultBranch()._id);

      if (!currentBranch.einvoices) {
        resolve(true)
        return
      }
      // trng hp cha c to kt ni ha n in t
      if (!currentBranch.einvoices?.length) {
        this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Please connect e-invoice for this branch"))
        resolve(true)
        return
      }
      // trng hp  gi ln cqt ri th khi
      if(bill.cqt_status == 2 || bill.cqt_status == 3){
        resolve(true)
        return
      }
      // trng hp ch c 1 kt ni ha n in t th to thng lun
      if (currentBranch.einvoices.length && currentBranch.einvoices.length == 1) {
        if (!currentBranch.einvoices[0].invoiceSeries.length) {
          this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Please connect e-invoice for this branch"))
          resolve(true)
          return
        }
        this.billService.changeBill_byLocal_toInvoice_byMinvoice(bill._id, currentBranch.einvoices[0].invoiceSeries[0])
          .then((rsp) => {
            console.log('changeBill_toInvoice_byMinvoice', rsp)
            if (rsp.vcode == 0) {
              if (this.cqt_invoice_auto_sign) {
                this.vhEinvoice.saveInvoice_Sign_byMinvoice(currentBranch.taxcode, currentBranch.einvoices[0].token, 1, rsp.data)
                  .then((res: any) => {
                    console.log('saveInvoice_Sign_byMinvoice', res);
                    if (res.code == '00') {
                      bill.cqt_status = 3
                      this.vhQueryCafe.updateBill(bill._id, { cqt_status: 3 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      }, error => {
                        console.log('error', error);
                      })
                    }
                    else if (res.code == '9999') {
                      bill.cqt_status = 1
                      this.vhQueryCafe.updateBill(bill._id, { cqt_status: 1 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      })
                      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng kim tra li thng tin ha n y ln CQT cha ph hp"))
                    }
                    else {
                      bill.cqt_status = 1
                      this.vhQueryCafe.updateBill(bill._id, { cqt_status: 1 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      })
                     
                      this.vhComponent.alertMessageDesktop("error", this.languageService.translate('An error occurred while sending the invoice to CQT'))
                    }
                  })
              }
              else {
                this.vhEinvoice.saveInvoice_noSign_byMinvoice(currentBranch.taxcode, currentBranch.einvoices[0].token, 1, rsp.data)
                  .then((res: any) => {
                    console.log(res);
                    if (res.code == '00') {
                      bill.cqt_status = 2
                      this.vhQueryCafe.updateBill(bill._id, { cqt_status: 2 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      }, error => {
                        console.log('error', error);
                      })
                    }
                    else if (res.code == '9999') {
                      
                      bill.cqt_status = 1
                      this.vhQueryCafe.updateBill(bill._id, { cqt_status: 1 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      })
                      this.vhComponent.alertMessageDesktop("error", this.languageService.translate("Vui lng kim tra li thng tin ha n y ln CQT cha ph hp"))
                    }
                    else {
                      
                      bill.cqt_status = 1
                      this.vhQueryCafe.updateBill(bill._id, { cqt_status: 1 }).then((bool: any) => {
                        console.log('updateBill', bool);
                        resolve(true)
                      })
                      this.vhComponent.alertMessageDesktop("error", this.languageService.translate('An error occurred while sending the invoice to CQT'))
                    }
                  })
              }
            }
          }, (error: any) => {
            console.log('error', error)
            resolve(true)
          })
      }
    })
  }

}
