import { Component, EventEmitter, Input, OnChanges, OnInit, Output, SimpleChanges } from '@angular/core';
import { VhQueryCafe } from 'ionic-vhframeworks';

@Component({
  selector: 'app-customer-profile',
  templateUrl: './customer-profile.component.html',
  styleUrls: ['./customer-profile.component.scss']
})
export class CustomerProfileComponent {
  @Input("id") customerID;
  @Input("showDrawerCustomerInfo") showDrawerCustomerInfo;
  @Output("closeInfoCustomer") closeInfoCustomer = new EventEmitter<any>();

  customer: any = {}
  constructor(
    private vhQueryCafe: VhQueryCafe
  ) { }

  ngOnChanges(change: SimpleChanges) {
    // ly d liu khch hng
    this.initCustomer(change.customerID.currentValue)
  }

  initCustomer(id_customer: string) {
    if (id_customer != 'id_retail') {
      let customer = this.vhQueryCafe.getlocalCustomer(id_customer)
      this.customer = customer ? customer : {}
    }
  }

  goBack() {
    this.closeInfoCustomer.emit(false)
  }
}
