import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { LanguageService } from 'src/app/cafe/services/language.service';
import { CountryCode } from 'libphonenumber-js/max';
import { FunctionService } from 'src/app/cafe/services/function.service';

@Component({
  selector: 'app-add-customer',
  templateUrl: './add-customer.component.html',
  styleUrls: ['./add-customer.component.scss']
})
export class AddCustomerComponent implements OnInit {
  @Input("showDrawerAddCustomer") showDrawerAddCustomer: boolean = false;
  @Output("closeAddCustomer") closeAddCustomer = new EventEmitter<any>();
  createStatus: boolean = false
  addCustomerForm: FormGroup;
  selectedValue: any;
  


  constructor(
    private vhComponent: VhComponent,
    private lang: LanguageService,
    private vhQueryCafe: VhQueryCafe,
    private functionService: FunctionService,
  ) { }



  ngOnInit() {
    this.initAddCustomerForm()
  }
  


  close(success = false) {
    if (!this.createStatus)
      this.closeAddCustomer.emit(success);
  }



  initAddCustomerForm() {
    this.addCustomerForm = new FormGroup({
      img: new FormControl(null),
      name: new FormControl(null, Validators.compose([Validators.required])),
      email: new FormControl(null, Validators.compose([Validators.email])),
      gender: new FormControl(null),
      datebirth: new FormControl(null),
      phone: new FormControl(null, [
        Validators.required,
        this.functionService.phoneValidator(() => this.countryCode),
      ]),
      address: new FormControl(null),
      country: new FormControl(null),
      province: new FormControl(null),
      district: new FormControl(null),
      note: new FormControl(null),
      type: new FormControl(1),
      time_for_payment: new FormControl(0),
      debt_enable: new FormControl(true),
      delete_hidden: new FormControl(false),
      isBusiness: new FormControl(false),
      taxcode: new FormControl(null),
    });
  }



  addCustomer() {
    this.createStatus = true
    let customer: any = { ...this.addCustomerForm.value };

    const phoneNumberValue = customer.phone;
    customer.countryCode = this.countryCode;
    customer.phone = this.functionService.getNationalPhoneNumber(phoneNumberValue, this.countryCode);
    customer.phoneNumber = this.functionService.getInternationalPhoneNumber(phoneNumberValue, this.countryCode);

    customer.date = (new Date()).toISOString();

    if(customer.isBusiness) {
      delete customer.gender;
      delete customer.datebirth;
    }
    else {
       delete customer.taxcode;
    }

    this.vhQueryCafe.addCustomer(customer)
      .then((res: any) => {
        if(res.vcode == 0) {
          setTimeout(() => {
            this.createStatus = false
            this.vhComponent.showToast(1500, `${this.lang.translate("Customer")} ${customer.name} ${this.lang.translate("has been added successfully")}`, "success-toast");
            this.close(true);
          }, 500)
        }
        else if(res.vcode == 1) {
          //This phone number is already in use with another customer
          this.createStatus = false
          this.isExitsPhone = true;
          this.vhComponent.showToast(1500, this.lang.translate("This phone number is already in use with another customer"), 'alert-toast');
        }
      }, error => {
        this.createStatus = false
        this.vhComponent.showToast(1500, this.lang.translate("An error occurred while adding customer. Please try again"), "alert-toast")
      });
  }



  /**
   * Khi thay i Loi khch hng
   */
  changeBusiness(event) {
    // taxcode required khi isBusiness = true
    if (event) {
      this.addCustomerForm.get('taxcode')?.setValidators([Validators.required]);
      this.addCustomerForm.get('taxcode')?.updateValueAndValidity();
    } 
    else {
      this.addCustomerForm.get('taxcode')?.clearValidators();
      this.addCustomerForm.get('taxcode')?.updateValueAndValidity();
    }
  }
  
  
  
  /** Kim tra s in thoi  tn ti ha cha  hin th thng bo */
  isExitsPhone: boolean = false;

  /** M quc gia */
  countryCode: string = this.functionService.defaultCountryCode;
  
  /** Calling code */
  callingCode: string = this.functionService.defaultCallingCode;
  
  /** S in thoi  format. VD: 333 333 333 */
  formattedPhone: string = '';



  /**
   * M modal chn m quc gia
   */
  openSearchCountryCode() {
    this.functionService.openSearchCountryCode().then((data) => {
      this.countryCode = data.code;
      this.callingCode = data.callingCode;
      this.formattedPhone = this.functionService.getFormattedPhoneNumber(this.addCustomerForm.value.phone, this.countryCode as CountryCode);
      this.addCustomerForm.get('phone')?.markAsTouched();
      this.addCustomerForm.get('phone')?.markAsDirty();
      this.addCustomerForm.get('phone')?.updateValueAndValidity();
    }).catch((error) => {
      console.error('Error opening country code search:', error);
    });
  }



  /**
   * Format s in thoi khi nhp v set vo form
   * @param event S kin khi nhp s in thoi
   */
  formatPhoneNumber(event: Event): void {
    // Ly s in thoi  format
    const input = (event.target as HTMLInputElement).value;
    this.formattedPhone = this.functionService.getFormattedPhoneNumber(input, this.countryCode as CountryCode);
    // Cp nht vo form
    this.addCustomerForm.get('phone')?.setValue(this.formattedPhone, { emitEvent: false });
  }

}
