import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm, VhQueryCafe } from 'ionic-vhframeworks';

@Component({
  selector: 'app-search-topping',
  templateUrl: './search-topping.component.html',
  styleUrls: ['./search-topping.component.scss']
})
export class SearchToppingComponent implements OnInit {
  // chng trnh khuyn mi ang c p dng
  @Input("promotions") promotions: any[] = []
  @Input("showDrawerSideDishesList") showDrawerSideDishesList: boolean = true;
  // tr v product c chn v  x l cho bill-detail1
  @Output("closeSearchSideDishesList") closeSearchSideDishesList: EventEmitter<any> = new EventEmitter();
  /**Danh sch topping */
  public sideDishesList: any[] = this.vhQueryCafe.getlocalProducts().filter(item => !item.delete_hidden && item.type == 4).map(e => { return { ...e, ...e.units[0] } })
  /**Topping c chn  x l */
  choiceSideDishes: any = {}
  /**Danh sch topping  c tm kim */
  public searchList: any[] = [];
  @Input() default_price_type: number
  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQueryCafe: VhQueryCafe, 
  ) { }
  ngOnInit(): void {
    this.searchList = [...this.sideDishesList]
    this.searchList = this.vhAlgorithm.sortVietnamesebyASC(this.searchList, 'name')
  }
  ngAfterViewInit() {
    document.getElementById("inputSearchSideDishes").focus();
  }

  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index
  }

  goBack() {
    this.closeSearchSideDishesList.emit(false);
  }
  tempVal: string = ''
  /**
   * Tm kim topping
   * @param value gi tr tm kim
   */
  searchSideDishes(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());

    if (value.length) this.searchList = this.vhAlgorithm.searchList(tempVal, this.sideDishesList, ['name'])
    else this.searchList = this.sideDishesList;
    this.searchList = this.vhAlgorithm.sortVietnamesebyASC(this.searchList, 'name')
  }

  /**
   * Chn topping  x l
   * @param value d liu topping c chn
   */
  selectSideDishes(value) {
    let dispatcher_done = !this.vhQueryCafe.getLocalAppSettingNameBranch("dispatchers_enable").dispatchers_enable
    this.choiceSideDishes = {
      dispatcher_done : dispatcher_done,
      ...value, quantity: 1, note: "", price_origin: this.default_price_type == 1 ? value.price : value.price2 ? value.price2 : value.price, price : this.default_price_type == 1 ? value.price : value.price2 ? value.price2 : value.price,ptype : 4
    }

    if (this.choiceSideDishes) {
      this.showOptions = true;
      setTimeout(() => {
        this.editPrice['price-' + value._id] = this.vhAlgorithm.vhnumeral(".price-" + value._id)
      }, 200)
    }
  }

  editPrice: any = {}
  /**Thm cleavejs chnh sa gi mn n */
  updatePrice(item) {
    let price = this.editPrice['price-' + item._id].getRawValue()
    this.choiceSideDishes.price = price ? parseFloat(price) : 0;
    this.choiceSideDishes.quantity = this.choiceSideDishes.quantity > 0 ? this.choiceSideDishes.quantity : 1
  }
  chooseSideDishes() {
    this.showOptions = false;
    if (this.choiceSideDishes['quantity']) {
      this.closeSearchSideDishesList.emit(this.choiceSideDishes)
    }
  }
 
  showOptions: boolean = false;
  selectFoodsCancel() {
    this.showOptions = false;
  }
}
