import { Component, Input, OnInit } from '@angular/core';
import { VhAuth, VhQueryCafe } from 'ionic-vhframeworks';
import { VhComponent } from 'src/app/cafe/components/vh-component/vh-component';
import { VhOrderInvoices } from 'src/app/cafe/interface/vh-order-invoice';
import { FunctionService } from 'src/app/cafe/services/function.service';
import { LanguageService } from 'src/app/cafe/services/language.service';

@Component({
  selector: 'app-info-bill1',
  templateUrl: './info-bill1.component.html',
  styleUrls: ['./info-bill1.component.scss']
})
export class InfoBill1Component implements OnInit {
  @Input() invoices: VhOrderInvoices;
  @Input() employeeID: string;
  @Input() tableInfo: any = {};
  pickerTime = new Date();
  employeeList: any[] = []
  constructor(
    public vhAuth: VhAuth,
    private vhQueryCafe: VhQueryCafe,
    private vhComponent: VhComponent,
    private languageService: LanguageService,
    private fncService: FunctionService
  ) { }

  ngOnInit(): void {
    this.initData()
  }

  myTrackByFunction(element: any, index: any) {
    return element._id ? element._id : index
  }

  initData() {
    // nhn vin
    this.employeeList = this.vhAuth.getlocalEmployees();
    this.pickerTime = new Date(this.invoices.getDate())
  }

  changeEmployee(ev) {
    this.invoices.setIdEmployee(ev)
  }

  onChangeDate(event) {
    this.vhQueryCafe.getValidityDate(event)
      .then((validitydate) => {
        if (validitydate) {
          this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 8000)
          this.invoices.setDate(this.invoices.getDate())
          this.pickerTime = new Date(this.invoices.getDate()) 
        }
        else {
          this.invoices.setDate(event);
        }
      })
  }
}
